import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutComponent } from './about/about.component';
import { HeaderComponent } from './header/header.component';
import { ChessComponent } from './hire-services/chess/chess.component';
import { HireServicesComponent } from './hire-services/hire-services.component';
import { ProgrammingComponent } from './hire-services/programming/programming.component';
import { WebComponent } from './hire-services/web/web.component';
import { ProjectsComponent } from './projects/projects.component';

const routes: Routes = [
  { path: 'projects', component: ProjectsComponent },

  //parent-child
  {
    path: 'hire-services',
    component: HireServicesComponent,
    children: [
      {
        path: 'chess',
        component: ChessComponent,
      },
      { path: 'web', component: WebComponent },
      { path: 'english', component: ProgrammingComponent },
    ],
  },

  { path: 'contact', component: AboutComponent },
  { path: '**', component: HeaderComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
